const handleFilterModalSubmit = async (value: any,pagenumber:any) => {
  try {
      const values = await form.validateFields(['enddate', 'prodcode', 'startdate']);
      if (title == 'Search') {
  setloader(true)

          let obj = {}
          if ( value && !value.prodcode&& !value.enddate && !value.startdate) {
              
              setIsModalOpen(false)
              return
          }
          if (searchPayload) {
              obj=value
          }
          if (value && value.prodcode) {
              obj = {
                  ...obj,
                  'prodcode': value.prodcode,
                  group:activeMenu=="BUYER"?1:activeMenu=="PRESET"?2:activeMenu=="BUYERWATCHLIST"?4:activeMenu=="UNDERCOSTLINE"?7:6
                
              }
          }
          if (value && value.enddate && value.startdate) {
              const sdate = convertdate(value.startdate)
              const edate = convertdate(value.enddate)
              
              obj={
                  ...obj,
                  sdate,
                  edate,
                  group:activeMenu=="BUYER"?1:activeMenu=="PRESET"?2:activeMenu=="BUYERWATCHLIST"?4:activeMenu=="UNDERCOSTLINE"?7:6
              }
          }
          let api = `/api/search-competitor-pricing/${pagenumber}/AsOfDate/0`
          let SearchProduct: any = await ApifordataPost(api, obj)
          setsearchPayload(obj)
          if (SearchProduct && SearchProduct.data && SearchProduct.data) {
            setflagforAction(false)
              setrowCnt(SearchProduct.data.rowCnt)
              if (SearchProduct.data.products.length >= 0) {
                setflagforAction(false)
                setData(SearchProduct.data.products)
              }
          }
  setloader(false)


      } else {
        downloadcsv(value)
      }
      setIsModalOpen(false)
      form.resetFields(["enddate", 'startdate', 'prodcode']);
  } catch (error) {
      console.error('Validation error:', error);
  }
};
const convertdate=(datePayload:any)=>{
  const date = new Date(datePayload);
  const year = date.getFullYear(); // Get the full year
  const month = ('0' + (date.getMonth() + 1)).slice(-2); // Months are zero-based, so add 1
  const day = ('0' + date.getDate()).slice(-2); // Get the day
  const formattedDate = `${year}-${month}-${day}`;
  return formattedDate
}
const downloadcsv =async (value:any)=>{
  let Api= `/api/download-competitor-pricing`
  let obj={}
  if (value && value.enddate && value.startdate) {
      const sdate = convertdate(value.startdate)
      const edate = convertdate(value.enddate)
      obj={
          ...obj,
          sdate,
          edate,
          group:activeMenu=="BUYER"?1:activeMenu=="PRESET"?2:activeMenu=="BUYERWATCHLIST"?4:activeMenu=="UNDERCOSTLINE"?7:6
      }
  }
  let downloadData: any = await ApifordataPost(Api,obj)
  console.log(downloadData,"downloadDatadownloadDatadownloadData")
  if(downloadData?.error && downloadData?.error=="No any competitor pricing data found")
    {
        message.error("downloadData.error")
        return
    }
  let TempArray:any=[]
  Object.keys(downloadData.data).map(date=>{
      let tempDate:any=date.split('-')
      let  finalDate=`${tempDate[2]}-${tempDate[1]}-${tempDate[0]}`
     
      let  newobj={
       RANKING:finalDate,
      'AGGCODE':'',
      'PRODCODE':'',
      'DESCRIPTION':'',
      "COST":"",
      "TRUE COST":"",
      "AVG COST":"",
      "AVG VOL":"",
      'PACK':'',
      'PHOENIX':'',
      'TRIDENT':'',
      'AAH':'',
      'COLORAMA':'',
      'BESTWAY':'',
      'SENSITIVITY':''
  }
  TempArray.push(newobj)

  
  downloadData.data[date].map((a:any)=>{
  
      let  newobj={
          RANKING:a.Ranking,
         'AGG CODE':a.ProductAC4,
         'PROD CODE':a.SPOTCode,
         'DESCRIPTION':a.ProductDesc,
         "COST":a.Cost,
         "TRUECOST":a.TrueCost,
         "AVGCOST":a.AvgCost,
         "AVGVOL":a.AvgVol,
         'PACK':a.PackSize,
         'PHOENIX':typeof(a.phoenix)=="string"?a.phoenix:"",
         'TRIDENT':typeof(a.trident)=="string"?a.trident:"",
         'AAH':typeof(a.aah)=="string"?a.aah:a.aah.price,
         'COLORAMA':typeof(a.colorama)=="string"?a.colorama:"",
         'BESTWAY':typeof(a.bestway)=="string"?a.bestway:"",
         'SENSITIVITY':a.sensitivity,

         aah_outofstock:a.aah_outofstock,
         colorama_outofstock:a.colorama_outofstock,
         phoenix_outofstock:a.phoenix_outofstock,
         trident_outofstock:a.trident_outofstock,
         bestway_outofstock:a.bestway_outofstock
     }
     TempArray.push(newobj)
   })


  })

  downloadExcelFile(TempArray)
}
const downloadExcelFile =async (data:any)=>{
  const workbook = new ExcelJS.Workbook();
  const worksheet = workbook.addWorksheet('Sheet1');
  const headers = [ 
      "RANKING",
      "AGG CODE",
      "PROD CODE",
      "DESCRIPTION",
      "COST",
      "TRUE COST",
      "AVG COST",
      "AVG VOL",
      "PACK",
      "PHOENIX",
      "TRIDENT",
      "AAH",
      "COLORAMA",
      "BESTWAY",
      "SENSITIVITY"
      ];
      worksheet.addRow(headers);
      data.forEach((row:any) => {
          const { 
              RANKING,
             
              'AGG CODE': AGGCODE, 
              'PROD CODE':PRODCODE,
              DESCRIPTION, 
              COST,
              'TRUECOST': TRUECOST,
              'AVGCOST': AVGCOST,
              'AVGVOL':AVGVOL,
              PACK,
              PHOENIX,
              TRIDENT,
              AAH,
              COLORAMA,
              BESTWAY,
              SENSITIVITY
           } = row;
          const rowValues = [
              RANKING,
              AGGCODE,
              PRODCODE, 
              DESCRIPTION, 
              COST,
              TRUECOST,
              AVGCOST,
              AVGVOL,

              PACK,
              PHOENIX,
              TRIDENT,
              AAH,
              COLORAMA,
              BESTWAY,
              SENSITIVITY
          ];
          const newRow = worksheet.addRow(rowValues);
          const aah = headers.indexOf('AAH') + 1;
          const PHOENIXt = headers.indexOf('PHOENIX') + 1;
          const TRIDENTt = headers.indexOf('TRIDENT') + 1;
          const COLORAMAt = headers.indexOf('COLORAMA') + 1;
          const BESTWAYt = headers.indexOf('BESTWAY') + 1;
         
          if (row && row.aah_outofstock && row.aah_outofstock==1 ) {
            newRow.getCell(aah).fill = {
              type: 'pattern',
              pattern: 'solid',
              fgColor: { argb: 'FFFF00' }, 
            };
          }
          if (row && row.phoenix_outofstock && row.phoenix_outofstock==1) {
            newRow.getCell(PHOENIXt).fill = {
              type: 'pattern',
              pattern: 'solid',
              fgColor: { argb: 'FFFF00' }, 
            };
          }
          if (row && row.trident_outofstock && row.trident_outofstock==1) {
            newRow.getCell(TRIDENTt).fill = {
              type: 'pattern',
              pattern: 'solid',
              fgColor: { argb: 'FFFF00' }, 
            };
          }
          if (row && row.bestway_outofstock && row.bestway_outofstock==1) {
            newRow.getCell(BESTWAYt).fill = {
              type: 'pattern',
              pattern: 'solid',
              fgColor: { argb: 'FFFF00' }, 
            };
          }
          if (row && row.colorama_outofstock && row.colorama_outofstock==1) {
            newRow.getCell(COLORAMAt).fill = {
              type: 'pattern',
              pattern: 'solid',
              fgColor: { argb: 'FFFF00' }, 
            };
          }
        });
  
        // Create the Excel file
        const buffer = await workbook.xlsx.writeBuffer();
  
        const blob = new Blob([buffer], { type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' });
        const url = window.URL.createObjectURL(blob);
        var tempLink = document.createElement('a')
        tempLink.href = url
        tempLink.setAttribute('download', `${'PriceReview'}.xlsx`)
        tempLink.click()
}
